:root {
  --light-white: #fff;
  --dark-black: #000;
  --navy-blue: #1a68d3;
  --light-blue: #71c7ee;
  --blur-color: #ffffff59;
}

* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

ul,
h1,
h2,
h3,
h4,
h5,
h6,
p {
  padding: 0px;
  margin: 0px;
}

img {
  max-width: 100%;
}

.main_wrapper {
  min-height: 100vh;
  background-color: #1a68d3;
  background-image: radial-gradient(ellipse at bottom, #1a68d3 5%, transparent 60%), linear-gradient(136deg, transparent, #71c7ee 290%), linear-gradient(115deg, #9d9af1, transparent 40%), linear-gradient(180deg, transparent 0, #1a68d3 70%), radial-gradient(ellipse at -70% -180%, transparent 80%, #71c7ee 0), radial-gradient(ellipse at bottom, #1a68d3 40%, transparent 80%), radial-gradient(ellipse at 5% 340%, transparent 80%, #bfc2f4 0);
  background-attachment: fixed;
  padding: 10px;
}

.container {
  max-width: 93%;
}

.vid-main-card {
  margin: 10px 0px;
  background-color: var(--blur-color);
  border-radius: 5px;
}
.vid-main-card .video-box {
  min-height: 13em;
  max-height: 13em;
  overflow: hidden;
}
.vid-main-card .User-bg-img {
  min-height: 13em;
  max-height: 13em;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.vid-main-card .User-bg-img:hover img {
  transform: scale(1.1);
}
.vid-main-card .User-bg-img img {
  border-radius: 50%;
  width: 60%;
  margin: auto;
  transition: all 1s ease-in-out;
}
.vid-main-card .detail-sec {
  background-color: var(--light-white);
  color: var(--dark-black);
  padding: 10px;
}
.vid-main-card .detail-sec .vidName {
  font-weight: 500;
}
.vid-main-card .detail-sec .channelName {
  font-size: 11px;
  color: var(--dark-black);
}
.vid-main-card .detail-sec .channelName.channel {
  font-size: 15px;
  font-weight: 500;
  color: var(--dark-black);
}

.topNav {
  background-color: var(--blur-color);
  border-radius: 10px;
  position: -webkit-sticky;
  position: sticky;
  z-index: 99;
  top: 0px;
  transition: all 0.7s ease-in-out;
}
.topNav .navbar-toggler {
  color: rgba(255, 255, 255, 0.8823529412);
}
.topNav .navbar-toggler:focus {
  color: #fff;
  text-decoration: none;
  outline: 0;
  text-shadow: 0 0 10px #fff;
  box-shadow: 0 0 0px 0px #fff;
}
.topNav .navbar-brand {
  display: flex;
  align-items: center;
  width: 4em;
  cursor: pointer;
}
.topNav .navbar-brand .img-sec {
  width: 70%;
}
.topNav .navbar-brand .text {
  color: var(--light-white);
}
.topNav .nav-item .nav-link {
  color: var(--light-white);
}
.topNav .SearchBar {
  position: relative;
}
.topNav .SearchBar .searcicon {
  border: 0px;
  position: absolute;
  background-color: rgba(107, 193, 236, 0.7803921569);
  right: -5px;
  color: #ffffff;
  border-radius: 50%;
  padding: 5px;
  top: 50%;
  cursor: pointer;
  transform: translate(-50%, -50%);
  transition: all 0.7s ease-in-out;
}
.topNav .SearchBar .form-control:focus .searcicon, .topNav .SearchBar .form-control:focus-within .searcicon, .topNav .SearchBar .form-control:hover .searcicon {
  background-color: #3dafe8;
}

.topNav.scroll {
  background-color: var(--light-white);
}
.topNav.scroll .navbar-toggler {
  color: var(--dark-black);
}
.topNav.scroll .navbar-toggler:focus {
  color: var(--dark-black);
  text-decoration: none;
  outline: 0;
  text-shadow: 0 0 10px #fff;
  box-shadow: 0 0 0px 0px #fff;
}
.topNav.scroll .nav-item .nav-link {
  color: var(--dark-black);
}
.topNav.scroll .navbar-brand .text {
  color: var(--dark-black);
}

.Main_home_sec {
  margin: 2em auto;
  color: #fff;
}

.chnl-Dtl-Wrpper .TopHeader {
  position: relative;
}
.chnl-Dtl-Wrpper .TopHeader .ChannelHeader {
  min-height: 15em;
  max-height: 15em;
  overflow: hidden;
}
.chnl-Dtl-Wrpper .TopHeader .ChannelHeader .CoverImg {
  width: 100%;
}
.chnl-Dtl-Wrpper .TopHeader .vid-main-card {
  top: 80%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
  background-color: transparent;
  width: 25%;
}
.chnl-Dtl-Wrpper .TopHeader .vid-main-card .User-bg-img {
  min-height: 15em;
  max-height: 15em;
}
.chnl-Dtl-Wrpper .TopHeader .vid-main-card .User-bg-img img {
  min-height: 80%;
  border: 2px solid #fff;
}
.chnl-Dtl-Wrpper .TopHeader .vid-main-card .detail-sec {
  background-color: transparent;
}
.chnl-Dtl-Wrpper .TopHeader .vid-main-card .detail-sec .channelName {
  color: var(--light-white);
}
.chnl-Dtl-Wrpper .TopHeader .vid-main-card .detail-sec .SubsCriberCount {
  color: #fff;
  text-align: center;
}
.chnl-Dtl-Wrpper .TopHeader .vid-main-card .detail-sec .SubsCriberCount .SubsNum {
  font-weight: 500;
  margin: 0px 3px;
}
.chnl-Dtl-Wrpper .VidDetail_box {
  margin-top: 6em;
}

.videoPlayerMain {
  margin: 1.5em 0px;
}
.videoPlayerMain .Video-list-wrapper {
  position: relative;
}
.videoPlayerMain .Video-list-wrapper .vidHolder {
  position: -webkit-sticky;
  position: sticky;
  top: 5.5em;
}
.videoPlayerMain .Video-list-wrapper .vidHolder .react-player {
  width: 100% !important;
  height: 458px !important;
}
.videoPlayerMain .Video-list-wrapper .vidHolder .vid-title {
  color: var(--light-white);
  font-weight: 500;
  margin: 10px 0px 0px;
  font-size: 20px;
}
.videoPlayerMain .Video-list-wrapper .ListHolder .card-holder {
  width: 100%;
}
.videoPlayerMain .Video-list-wrapper .ListHolder .card-holder .vid-main-card {
  margin: 0px 0px 10px;
}/*# sourceMappingURL=styles.css.map */